$foreground-color: var(--react-var-ui-foreground-color, #ddd);
$background-color: var(--react-var-ui-background-color, #11111a);
$accent-color: var(--react-var-ui-accent-color, #77f);
$input-background-color: var(--react-var-ui-input-background-color, #353542);
$input-background-hover-color: var(
  --react-var-ui-input-background-hover-color,
  #424253
);
$input-background-pressed-color: var(
  --react-var-ui-input-background-pressed-color,
  #2b2b37
);
$label-background-normal-color: var(
  --react-var-ui-label-background-normal-color,
  #22222a
);
$label-background-hover-color: var(
  --react-var-ui-label-background-hover-color,
  #2a2a33
);
$label-border-color: var(--react-var-ui-label-border-color, #33333a);
$transition-time: 0.2s;

.react-var-ui {
  background: $background-color;

  &-add {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;

    &-wrapper {
      position: relative;
      height: 22px;
      width: 22px;
      cursor: pointer;
    }

    &-popover {
      position: absolute;
      z-index: 2;
      right: 0;
    }
  }

  &-group {
    padding-left: 0.8rem;
    position: relative;

    &-wrapper {
      display: flex;
      flex-direction: row-reverse;
      align-items: center;

      &-icon {
        position: relative;
        height: 20px;
        width: 20px;
        cursor: pointer;
      }
    }

    & &-item:first-child:before {
      position: absolute;
      top: -12px;
      bottom: -40px;
      margin-left: -8px;
      border-left: 1px solid #515668;
      content: ' ';
    }

    & &-item:first-child:after {
      position: absolute;
      width: 6px;
      height: 50%;
      top: 0;
      margin-left: -8px;
      border-bottom: 1px solid #515668;
      content: ' ';
    }

    & &-item:not(:first-child):not(:last-child):before {
      position: absolute;
      top: 0;
      bottom: 0;
      margin-left: -8px;
      content: ' ';
      border-left: 1px solid #515668 !important;
    }

    & &-item:not(:first-child):not(:last-child):after {
      position: absolute;
      width: 6px;
      height: 50%;
      top: 0;
      margin-left: -8px;
      border-bottom: 1px solid #515668;
      content: ' ';
    }

    & &-item:last-child:before {
      position: absolute;
      top: 0;
      height: 50%;
      margin-left: -8px;
      content: ' ';
      border-left: 1px solid #515668 !important;
    }

    & &-item:last-child:after {
      position: absolute;
      width: 6px;
      height: 50%;
      top: 0;
      margin-left: -8px;
      border-bottom: 1px solid #515668;
      content: ' ';
    }

    &-item {
      display: block;
      position: relative;
    }
  }

  &-disabled {
    pointer-events: none;
    opacity: 0.5;
  }

  &-image {
    display: flex;
    flex-direction: row;
    align-items: center;

    &-wrapper {
      position: relative;
      height: 34px;
      width: 34px;
      box-sizing: border-box;
      border: 1px solid #515151;
      border-radius: 6px;
      overflow: hidden;
      cursor: pointer;

      &-preview {
        height: 32px;
        width: 32px;
      }

      input {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        opacity: 0;
        cursor: pointer;
      }
    }

    &-delete {
      margin-left: 14px;
      cursor: pointer;
    }
  }

  &-category {
    &-title {
      color: $foreground-color;
      text-transform: uppercase;
      font-size: 1.125rem;
      font-weight: 600;
      padding: 1.25rem;
      user-select: none;
      position: relative;

      &-dash {
        &:before {
          content: '';
          border-top: 1px dashed #6a7187;
          width: 100%;
          position: absolute;
          opacity: 0.6;
          top: 0;
          left: 0;
          right: 0;
        }
      }
    }

    padding-bottom: 1rem;
  }

  &-color {
    display: flex;
    align-items: center;
    position: relative;
    margin-left: 1rem;

    .sketch-picker {
      position: absolute;
      top: 1rem;
      right: -3.5rem;
    }

    &-color {
      position: relative;
      width: 2.5rem;
      height: 1.2rem;
      border-radius: 0.2rem;
      border: 1px solid #515668;
    }

    &-value {
      width: 3.75rem;
    }

    &-swatch {
      border-radius: 0.2rem;
      display: inline-block;
      cursor: pointer;
      background: white
        url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAAXNSR0IArs4c6QAAAARzQklUCAgICHwIZIgAAAAxSURBVDhPY2RgYBABYnzgDT5JxlEDGIZJGBBIBvjTCSgMCAG8CW3UAAZwQiIEaBuIACBfCLFwf0bOAAAAAElFTkSuQmCC')
        center center;
    }

    &-popover {
      position: absolute;
      z-index: 2;
    }

    &-cover {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }

  &-toggle {
    position: relative;
    width: 2.125rem;
    height: 1.125rem;

    &-helper {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: $accent-color !important;
      border-radius: 0.2rem;
      transition: $transition-time ease-in-out all;
      cursor: pointer;

      &:before {
        position: absolute;
        height: 0.875rem;
        width: 0.9375rem;
        left: 0.1rem;
        top: 0.1rem;
        border-radius: 0.375rem;
        background-color: #fff;
        content: '';
        transition: $transition-time ease-in-out all;
      }
    }

    input {
      width: 0;
      height: 0;
      opacity: 0.01;

      &:checked + .react-var-ui-toggle-helper {
        background: #0dbe54 !important;

        &:before {
          transform: translateX(0.9rem);
        }
      }
    }
  }

  &-select {
    select {
      width: 100%;
      background: $input-background-color;
      border: none;
      border-radius: 0.2rem;
      color: $foreground-color;
      padding: 0.2rem 0.5rem;

      &:hover,
      &:focus {
        background: $input-background-hover-color;
      }
    }
  }

  &-string {
    &-multiline,
    input {
      width: 100%;
      min-width: 100%;
      max-width: 100%;
      background: $input-background-color;
      border: none;
      border-radius: 0.2rem;
      color: $foreground-color;
      padding: 0.2rem 0.5rem;
      outline: none !important;
      box-sizing: border-box;
      border-radius: 0.4rem;

      &:hover,
      &:focus {
        background: $input-background-hover-color;
      }
    }

    &-multiline {
      grid-column: 1 / 3;
      margin-top: 0.4rem;
      font-family: inherit;
      padding: 0.5rem;
      font-size: 0.8rem;
      min-height: 2rem;
    }
  }

  &-label {
    display: grid;
    grid-template-columns: 1fr 2fr;
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    color: $foreground-color;
    background: $label-background-normal-color;
    border-bottom: 1px solid $label-border-color;
    user-select: none;

    &:first-child {
      border-top: 1px solid $label-border-color;
    }

    &:hover {
      background: $label-background-hover-color;
    }

    & > span {
      display: flex;
      align-items: center;

      &:last-child {
        justify-content: flex-end;
      }
    }

    &-has-label {
      & > span {
        &:first-child {
          margin-right: 0.2rem;
        }
      }
    }

    &-no-label {
      grid-template-columns: 1fr;
    }
  }

  &-slider,
  &-number {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;

    &-track {
      background: $input-background-color;
      position: relative;
      flex: 1;
      height: 1.3rem;
      border-radius: 0.2rem;
      overflow: hidden;
      cursor: pointer;
      margin-right: 0.4rem;
      touch-action: none;
    }

    &-content {
      background: $accent-color;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      pointer-events: none;
    }

    input {
      width: 2.5rem;
      background: #383c4a;
      border: none;
      -moz-appearance: textfield;
      color: $foreground-color;
      outline: none !important;
      height: 1.3rem;
      padding: 0 0.15rem 0 0.2rem;
      text-align: right;
      border-radius: 0.26rem;

      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
    }

    span {
      width: 2.5rem;
      text-align: right;
    }

    button {
      background: $label-background-normal-color;
      border: none;
      cursor: pointer;
      color: $foreground-color;
      height: 1.3rem;
      margin-left: 0.4rem;
      border-radius: 0.25rem;

      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &-vector {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 -5px;

    &-wrapper {
      height: 1.3rem;
      margin: 0 3px;
      flex: 1 1 0;
      background: #383c4a;
      border-radius: 0.25rem;
      display: flex;
      align-items: center;
      box-sizing: border-box;
      overflow: hidden;

      span {
        margin-left: 0.3rem;
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        color: #f28f1b;
        text-shadow: 0px 0px 4px  rgba(0, 0, 0, 0.25);
      }

      input {
        flex: 1;
        background: #383c4a;
        width: 100%;
        border: none;
        -moz-appearance: textfield;
        color: $foreground-color;
        outline: none !important;
        height: 1.3rem;
        padding: 0 0.2rem;
        text-align: center;

        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
          margin: 0;
        }
      }
    }
  }

  &-number {
    input {
      background: $input-background-color;

      &:hover,
      &:focus {
        background: $input-background-hover-color;
      }
    }
  }

  &-xy {
    grid-column: 2 / 3;

    &-value {
      display: inline-block !important;
      text-align: right;
    }

    &-space {
      margin-top: 0.5rem;
      margin-bottom: 0.3rem;
      width: 100%;
      height: 150px;
      background: $input-background-color;
      position: relative;
      cursor: pointer;
      border-radius: 0.4rem;
      overflow: hidden;
      touch-action: none;
    }

    &-control {
      background: $accent-color;
      width: 1rem;
      height: 1rem;
      border-radius: 100%;
      transform: translate(-50%, -50%);
      position: absolute;
      pointer-events: none;

      &:after,
      &:before {
        content: '';
        position: absolute;
        background: $accent-color;
        opacity: 0.5;
      }

      &:before {
        top: -100rem;
        bottom: -100rem;
        left: calc(50% - 1px);
        width: 1px;
      }

      &:after {
        left: -100rem;
        right: -100rem;
        top: calc(50% - 1px);
        height: 1px;
      }
    }
  }

  &-angle {
    grid-column: 1 / 3;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1rem;

    &-value {
      justify-content: flex-end;
    }

    &-control {
      width: 6rem;
      height: 6rem;
      background: $input-background-color;
      position: relative;
      border-radius: 100%;
      cursor: pointer;
      touch-action: none;

      &:after {
        display: block;
        position: absolute;
        left: calc(50% - 0.5rem);
        top: 0.5rem;
        width: 1rem;
        height: 1rem;
        background: $accent-color;
        border-radius: 100%;
        content: '';
      }
    }
  }

  &-button {
    button {
      width: 100%;
      color: $foreground-color;
      background: $input-background-color;
      border: none;
      border-radius: 0.4rem;
      padding: 0.4rem 1rem;
      cursor: pointer;

      &:hover {
        background: $input-background-hover-color;
      }

      &:active {
        background: $input-background-pressed-color;
      }
    }
  }
}
